<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <router-link to="/">去首页</router-link>
  </div>
</template>

<script>
export default {
  name: "NotFound",
  data() {
    return {
      msg: "抱歉，找不到页面"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
</style>



